import styles from "./WordCloud.module.css";
import { useState } from "react";
import propic from "../1501V.jpg";
import fatworm from "../IMG_5380.PNG";
import Flip from "react-reveal/Flip";
import Fade from "react-reveal/Fade";

function WordCloud() {
  const [open, setOpen] = useState(false);
  function clickHandler() {
    setOpen(!open);
  }
  return (
    <div className={styles.background}>
      <div className={styles.foreground}>
        <Fade top>
          <p className={styles.name}>YVONE CHAN</p>

          <p className={styles.description}>
            Pharmacist / Programmer / Project Manager
          </p>
        </Fade>
        <Flip right opposite>
          <div className={styles.propic} onClick={clickHandler}>
            {open ? (
              <img src={propic} alt="portrait" className={styles.photo}></img>
            ) : (
              <img src={fatworm} alt="portrait" className={styles.photo}></img>
            )}
          </div>
        </Flip>
      </div>
    </div>
  );
}

export default WordCloud;
